<template>
  <div class="bg-light">
    <div class="container">
      <div class="row py-8">
        <div class="col-4">
          <div class="searchBox">
            <div class="searchBox-header">
              <h3 class="fw-bold"></h3>
            </div>
            <div class="searchBox-body">
              <label for="searchProductDestination"></label>
              <input
                type="search"
                id="searchProductDestination"
                placeholder=""
                class="mb-3"
                v-model="searchProduct"
              />
              <select name="activityPeriod" class="mb-6">
                <option value="default" selected disabled></option>
                <option value="lessThan4hrs"> 4 </option>
                <option value="lessThan8hrs">4 - 8 </option>
                <option value="2DaysTrip"></option>
                <option value="3DaysTrip"></option>
              </select>
              <div class="pb-6 mb-6 border-bottom border-gray">
                <h4 class="mb-6 h3"></h4>
                <vue-slider
                  v-model="budget"
                  v-bind="options"
                  :tooltip-formatter="'NT {value}'"
                ></vue-slider>
              </div>
              <div
                class="d-flex flex-wrap btn-group"
                role="group"
                aria-label="Basic checkbox toggle button group"
              >
                <div class="me-2 mb-2" v-for="(item, key) in tagCategory" :key="key">
                  <input type="checkbox" :id="item" autocomplete="off" class="btn-check" />
                  <label :for="item" class="btn btn-outline-secondary rounded-3"
                    ># {{ item }}
                  </label>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="col-8">
          <div class="d-flex justify-content-between mb-6">
            <h2 v-if="searchProduct === ''">
              
              <span class="h3 ms-5" v-if="totalProducts.length == 0"></span>
              <span class="h3 ms-5" v-else> {{ totalProducts.length }} </span>
            </h2>
            <h2 v-else>
              {{ searchProduct }}
              <span class="h3 ms-5" v-if="filterProduct.length == 0"></span>
              <span class="h3 ms-5" v-else> {{ filterProduct.length }} </span>
            </h2>
            <select
              name="productArrangement"
              class="arrangementSelect"
              @change="productArrangment($event)"
            >
              <option value="default" selected></option>
              <option value="expensiveToCheap"></option>
              <option value="cheapToExpensive"></option>
            </select>
          </div>
          <ul class="mb-8">
            <li class="mb-3" v-for="item in filterProduct" :key="item.id">
              <router-link :to="`/product/${item.id}`" class="card">
                <img class="card-img" :src="item.imageUrl" :alt="item.title" />
                <div class="card-body">
                  <div>
                    <h5 class="card-title">{{ item.title }}</h5>
                    <p>{{ item.description }}</p>
                  </div>
                  <p class="text-end">
                    <span class="h3">NT {{ item.lowestPrice }} </span>
                    / {{ item.lowestPriceUnit }}
                  </p>
                </div>
              </router-link>
            </li>
          </ul>
          <!--pagination-->
          <pagination
            :page="pagination"
            @emit-pagination="getData"
            v-if="pagination.total_pages > 1"
          ></pagination>
        </div>
      </div>
      <!--alert-->
      <alert v-if="showAlert" :alert-msg="alertMsg"></alert>
    </div>
  </div>
</template>

<script>
import alert from '@/components/Alert.vue';
import pagination from '@/components/Pagination.vue';
import VueSlider from 'vue-slider-component';

export default {
  name: 'Product',
  data() {
    return {
      products: [],
      totalProducts: [],
      budget: [1000, 6000],
      options: {
        dotSize: 12,
        width: 'auto',
        height: 2,
        min: 0,
        max: 10000,
        duration: 0.5,
        tooltip: 'active',
        tooltipPlacement: 'top',
      },
      tagCategory: [
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
      ],
      searchProduct: '',
      pagination: {},
    };
  },
  components: { alert, pagination, VueSlider },
  methods: {
    getData(currentPage = 1) {
      // 
      this.$http
        .get(
          `${process.env.VUE_APP_API}/api/${process.env.VUE_APP_PATH}/products/?page=${currentPage}`,
        )
        .then((res) => {
          if (res.data.success) {
            this.products = res.data.products;
            this.products.forEach((item, index) => {
              // 
              let packageOptionsPrice = item.packageOptions.map((i) => i.price); // 
              packageOptionsPrice = packageOptionsPrice.sort((x, y) => x - y); // 
              // this.products[index].lowestPrice = packageOptionsPrice[0];
              const [lowestPrice] = packageOptionsPrice; // 
              this.products[index].lowestPrice = lowestPrice;
              // 
              item.packageOptions.forEach((i) => {
                if (i.price === lowestPrice) {
                  this.products[index].lowestPriceUnit = i.unit;
                }
              });
            });
            this.pagination = res.data.pagination;
          } else {
            this.customAlert(res.data.message);
          }
        })
        .catch((err) => {
          this.customAlert(err.response);
        });
      this.$http
        .get(`${process.env.VUE_APP_API}/api/${process.env.VUE_APP_PATH}/products/all`)
        .then((res) => {
          if (res.data.success) {
            this.totalProducts = res.data.products;
            this.totalProducts.forEach((item, index) => {
              // 
              let packageOptionsPrice = item.packageOptions.map((i) => i.price); // 
              packageOptionsPrice = packageOptionsPrice.sort((x, y) => x - y); // 
              // this.products[index].lowestPrice = packageOptionsPrice[0];
              const [lowestPrice] = packageOptionsPrice; // 
              this.totalProducts[index].lowestPrice = lowestPrice;
              // 
              item.packageOptions.forEach((i) => {
                if (i.price === lowestPrice) {
                  this.totalProducts[index].lowestPriceUnit = i.unit;
                }
              });
            });
          } else {
            this.customAlert(res.data.message);
          }
        })
        .catch((err) => {
          this.customAlert(err.response);
        });
    },
    customAlert(msg) {
      this.alertMsg = msg;
      this.showAlert = true; //  alert
    },
    closeCustomAlert() {
      this.showAlert = false;
    },
    productArrangment(e) {
      let priceArr = this.totalProducts.map((item) => item.lowestPrice);
      switch (e.target.value) {
        case 'expensiveToCheap':
          priceArr = priceArr.sort((x, y) => y - x);
          this.totalProducts.forEach((item) => {
            priceArr.forEach((price, index) => {
              if (item.lowestPrice === price) {
                this.totalProducts[index] = item;
              }
            });
          });
          console.log(this.totalProducts);
          break;
        case 'cheapToExpensive':
          this.getData();
          break;
        default:
          this.getData();
          break;
      }
    },
  },
  computed: {
    filterProduct() {
      if (this.searchProduct !== '') {
        return this.totalProducts.filter((item) => item.title.match(this.searchProduct));
      }
      return this.products;
    },
  },
  created() {
    this.getData();
  },
};
</script>
